import React from "react";
import { Button } from "@mui/material";

const FormikButton = (props) => {
  const bg = props.bg;
    
  return (
    <Button
      data-testid={props.testId}
      style={{
        backgroundColor: `${bg}`,
        color: "white",
        marginTop: "1em",
        fontFamily: "ITC Bauhaus font",
        // fontFamily: "Roboto",
        fontSize: "25px",
        textTransform: "none",
        padding: "5px 20px 5px 20px",
        borderRadius: "10px",
      }}
      type={props.type}
      onClick={props.onClick}
    >
      {props.btnText}
    </Button>
  );
};

export default FormikButton;
